<template>
  <div class="wsy_container">
    <div class="wsy_top">
      <span @click="$router.back(-1)">
        <van-icon name="arrow-left" />
      </span>
      <span>个人</span>
    </div>
    <p>用户{{ user }}您好</p>
    <van-button type="danger" size="large" @click="quit()">退出登录</van-button>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      user: ""
    };
  },
  created() {},
  mounted() {
    this.getUserInfo();
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    //   获取用户信息
    getUserInfo() {
      let data = window.localStorage.getItem("user_Token");
      this.user = JSON.parse(data);
    },
    //   退出登陆
    quit() {
        window.localStorage.removeItem("user_Token")
        this.$router.push("/elm/login")
    }
  },
  filters: {},
  computed: {},
  watch: {}
};
</script>

<style lang='scss' scoped>
.wsy_container {
  width: 100%;
  height: 100%;
  > .wsy_top {
    padding: 0 0.2rem;
    height: 46px;
    line-height: 46px;
    font-size: 18px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #3190e8;
    color: #fff;
  }
}
</style>
