<template>
  <div class="wsy_login">
    <div class="wsy_top">
      <span @click="$router.back(-1)">
        <van-icon name="arrow-left" />
      </span>
      <span>密码登录</span>
    </div>
    <div class="wsy_from">
      <p>
        <input type="text" placeholder="用户名" v-model.trim="username" />
      </p>
      <p>
        <input
          :type="!flag ? 'password':'text'"
          maxlength="6"
          placeholder="密码"
          v-model.trim="password"
        />
        <S @toggle="toggle" />
      </p>
      <p>
        <input type="text" placeholder="验证码" maxlength="4" v-model.trim="verify" />
        <img :src="imgUrl" alt />
        <a href="#" @click="getVerify()">换一张</a>
      </p>
      <p class="warring">温馨提示：未注册过的账号，登录时将自动注册</p>
      <p class="warring">注册过的用户可凭账号密码登录</p>
      <van-button type="primary" :disabled="!isDisabled" block @click="submit()">登陆</van-button>
    </div>
  </div>
</template>

<script>
import S from "./Switch";
import axios from "axios"
export default {
  name: "",
  components: {
    S
  },
  props: {},
  data() {
    return {
      flag: false,
      imgUrl: "",
      username: "15076978269",
      password: "123456",
      verify: ""
      // isDisabled:true
    };
  },
  created() {},
  mounted() {
    this.getVerify();
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    //   页面加载获取验证码图片
    getVerify() {
      this.$axios.post("/v1/captchas").then(({ data }) => {
        this.imgUrl = data.code;
      });
    },
    //   切换
    toggle(data) {
      this.flag = data;
    },
    //   提交
    submit() {
      let reg = /^1(3[0-9]|5[0-3,5-9]|7[1-3,5-8]|8[0-9])\d{8}$/;
      if (reg.test(this.username) && this.password != "" && this.verify != "") {
        this.setCookie("cap", this.verify, 2);
        this.setCookie("SID", 100, 2);
        axios
          .post("/api/v2/login", {
            captcha_code: this.verify,
            username: this.username,
            password: this.password
          })
          .then(({status,data}) => {
            if (status == 200) {
                console.log(data);
                window.localStorage.setItem("user_Token",JSON.stringify(data.username))
              this.$toast.success("登陆成功");
              this.$router.push("/elm");
            } else {
              this.$toast.fail("登陆失败");
            }
          });
        //   this.$toast.success('');
      }
    },
    setCookie(key, value, t) {
      var oDate = new Date();
      oDate.setDate(oDate.getDate() + t);
      document.cookie = key + "=" + value + "; expires=" + oDate.toDateString();
    }
    //   captcha_code: "1222"
    // password: "123456"
    // username: "15076978269"
  },
  filters: {},
  computed: {
    isDisabled() {
      return this.username !== "" && this.password !== "" && this.verify !== "";
    }
  },
  watch: {}
};
</script>

<style lang='scss' scoped>
.wsy_login {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.wsy_top {
  padding: 0 0.2rem;
  height: 46px;
  line-height: 46px;
  font-size: 18px;
  background-color: #3190e8;
  color: #fff;
  text-align: center;
  > :nth-child(1) {
    float: left;
  }
}
.wsy_from {
  flex: 1;
  background: #f5f5f5;
  > :nth-child(1),
  :nth-child(2) {
    margin: 0;
    width: 100%;
    height: 40px;
    background-color: #fff;
    border-bottom: 1px solid #000;
    > input {
      width: 100%;
      height: 90%;
      border: 0;
    }
  }
  > :nth-child(2) {
    display: flex;
    position: relative;
  }
  > :nth-child(3) {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    > input {
      width: 50%;
      height: 100%;
    }
    > img {
      width: 30%;
    }
  }
}
.warring {
  color: red;
}
</style>
