<template>
  <div class="wsy_box">
    <Header>
      <div slot="left">
        <van-icon name="search" @click='toSearch()' />
      </div>
      <div slot="title">
        <p class="Header_title">{{ City.name }}</p>
      </div>
    </Header>
    <div class="wsy_parent" ref="scrollBox">
      <div class="wsy_cont">
        <!-- 轮播图区域 -->
        <van-swipe class="my-swipe" indicator-color="#007AFF">
          <van-swipe-item>
            <div
              v-show="index<8"
              class="wsy_banner_item"
              v-for="(item,index) in Banner"
              :key="index"
            >
              <img :src="'https://fuss10.elemecdn.com'+item.image_url" alt />
              <span>{{ item.title }}</span>
            </div>
          </van-swipe-item>
          <van-swipe-item>
            <div
              v-show="index>7"
              class="wsy_banner_item"
              v-for="(item,index) in Banner"
              :key="index"
            >
              <img :src="'https://fuss10.elemecdn.com'+item.image_url" alt />
              <span>{{ item.title }}</span>
            </div>
          </van-swipe-item>
        </van-swipe>
        <!-- 列表区域 -->
        <div class="wsy_list">
          <div class="wsy_list_header">
            <p>
              <van-icon name="shop-o" />附近商家
            </p>
          </div>
          <ul>
            <li v-for="(item,index) in ShopList" :key="index">
              <div class="left_img">
                <img
                  src="https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=3764959624,4269662245&fm=26&gp=0.jpg"
                  alt
                />
              </div>
              <div class="list_right">
                <div class="list_right_top">
                  <p>
                    <span>品牌</span>
                    <span>{{ item.name }}</span>
                  </p>
                  <p>保准票</p>
                </div>
                <div class="list_right_middle">
                  <div>
                    <van-rate
                      v-model="item.rating"
                      :size="10"
                      color="#ffd21e"
                      void-icon="star"
                      void-color="#eee"
                    />
                    <span>{{ item.rating }}</span>
                    <span>月售{{ item.recent_order_num }}单</span>
                  </div>
                  <p>
                    <span>{{ item.delivery_mode.text }}</span>
                    <span>准时达</span>
                  </p>
                </div>
                <div class="list_right_bottom">
                  <p>￥{{ item.float_minimum_order_amount }}起送/{{ item.piecewise_agent_fee.tips }}</p>
                  <p>
                    <span>{{ item.distance }}/</span>
                    <span>{{ item.order_lead_time }}</span>
                  </p>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="white">

    </div>
    <!-- <van-tabbar v-model="active" active-color="#25ABFF" inactive-color="#6C6C6C">
      <van-tabbar-item icon="home-o" to='/elm/Home'>首页</van-tabbar-item>
      <van-tabbar-item icon="search" to='/elm/search'>搜索</van-tabbar-item>
      <van-tabbar-item icon="friends-o" to='/elm/indent'>订单</van-tabbar-item>
      <van-tabbar-item icon="setting-o" to='/elm/my'>我的</van-tabbar-item>
    </van-tabbar> -->
  </div>
</template>

<script>
import Header from "@/components/Header";
import BetterScroll from "better-scroll";

export default {
  name: "",
  components: {
    Header
  },
  props: {},
  data() {
    return {
      City: {},
      bs: null,
      active: 0,
      Banner: [],
      ShopList: []
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        this.bs = new BetterScroll(this.$refs.scrollBox, {
          probeType: 3,
          click: true
        });
      },1500);
    }),
      this.getCityTitle();
    this.getBanner();
    this.getShopList();
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    // 获取城市详情页标题
    getCityTitle() {
      let city = JSON.parse(window.localStorage.getItem("City"))
      this.City = this.$router.query == undefined ? city : this.$route.query
    },
    // 获取轮播图数据
    async getBanner() {
      let { status, data } = await this.$axios.get(
        `/v2/index_entry?geohash=${this.City.geohash}&group_type=1&flags[]=F`
      );
      if (status == 200) {
        this.Banner = data;
      }
    },
    // 获取商品列表数据
    async getShopList() {
      let { status, data } = await this.$axios.get(
        `/shopping/restaurants?latitude=${this.City.latitude}&longitude=${this.City.longitude}&offset=0&limit=20&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=`
      );
      if (status == 200) {
        this.ShopList = data;
        console.log(data);
      }
    },
    // 跳转到搜索页面
    toSearch(){
      this.$router.push("/elm/search")
    }
  },
  filters: {},
  computed: {},
  watch: {}
};
</script>

<style lang='scss'  scoped>
.header {
  width: 100%;
  //   box-sizing: border-box;
}
.Header_title {
  width: 200px;
  margin: 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.wsy_box {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.wsy_parent {
  width: 100%;
  flex: 1;
  overflow: hidden;
}
.wsy_cont {
  width: 100%;
  min-height: 101%;
  background-color: #f5f5f5;
}
// 轮播图样式
.my-swipe .van-swipe-item {
  font-size: 20px;
  text-align: center;
  background: #fff;
}
.van-swipe-item {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  > .wsy_banner_item {
    box-sizing: border-box;
    margin: 20px 0;
    width: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    > img {
      width: 60%;
    }
    > span {
      color: #666666;
      font-size: 14px;
    }
  }
}
.wsy_list {
  width: 100%;
  background-color: #fff;
  margin-top: 10px;
  > ul {
    width: 100%;
    display: flex;
    flex-direction: column;
    > li {
      width: 100%;
      height: 120px;
      box-sizing: border-box;
      padding: 10px;
      border-bottom: 1px solid #f1f1f1;
      display: flex;
      align-items: center;
      justify-content: space-between;
      > .left_img {
        width: 20%;
        > img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  > .wsy_list_header {
    height: 40px;
    width: 100%;
    line-height: 40px;
    font-size: 14px;
    padding: 0 0.2rem;
    color: #999999;
    > p {
      margin: 0;
    }
  }
}
.list_right {
  width: 100%;
  display: flex;
  flex-direction: column;
  font-size: 14px;
  box-sizing: border-box;
  padding-left: 0.2rem;
  > .list_right_top {
    width: 100%;
    display: flex;
    justify-content: space-between;
    > p {
      margin: 0;
    }
    > :nth-child(1) {
      > :nth-child(1) {
        background: #ffd930;
        margin-right: 0.1rem;
      }
      > :nth-child(2) {
        width: 100px;
        font-weight: bold;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }
    }
    > :nth-child(2) {
        font-size: 10px;
      color: #999999;
      border: 1px solid #999999;
    }
  }
  > .list_right_middle {
    display: flex;
    justify-content: space-between;
    align-items: center;
    > :nth-child(1) {
      > :nth-child(2) {
        color: red;
        margin: 0 0.1rem;
      }
      > :nth-child(3) {
        font-size: 10px;
        color: #666666;
      }
    }
    > :nth-child(2) {
      > :nth-child(1) {
        background: #007aff;
        color: #fff;
        margin-right: 0.1rem;
      }
      > :nth-child(2) {
        border: 1px solid #007aff;
        color: #007aff;
      }
    }
  }
  > .list_right_bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    > p {
      margin: 0;
      font-size: 12px;
    }
    > :nth-child(1) {
      color: #666666;
    }
    > :nth-child(2) {
      > :nth-child(1) {
        color: #999999;
      }
      > :nth-child(2) {
        color: #007aff;
      }
    }
  }
}
.white{
  height: 50px;
}
</style>
