<template>
  <div class="wsy_container">
    <div class="wsy_top">
      <p @click="$router.back(-1)">
        <van-icon name="arrow-left" />
      </p>
      <p>{{ city.name }}</p>
      <p @click="$router.back(-1)">切换城市</p>
    </div>
    <div class="wsy_cont">
      <van-search v-model="keyword" placeholder="输入学校、商务楼、地址" @search="search()" />
      <van-button type="info" block @click='search()'>搜索</van-button>
      <van-icon name="cross" v-show="keyword.length!=0" @click='keyword = ""' />
    </div>
    <div class="historyList" v-show="!flag">
      <p>历史记录 
        <van-icon name="delete" @click='clearHistory' />
      </p>
      <ul>
        <li v-for='(item,index) in historyList' :key="index" @click="addHistory(item)">{{ item.name }}</li>
      </ul>
    </div>
    <div class="wsy_searchList" v-show="flag">
      <ul>
        <li v-for='(item,index) in searchList' :key='index' @click="addHistory(item)">
          <p>{{ item.name }}</p>
          <p>{{ item.address }}</p>
        </li>
      </ul>
    </div>
    <div></div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      cityName: "",
      city: {},
      keyword: "",
      searchList:[],
      historyList:[],//历史记录
      flag:false
    };
  },
  created() {},
  mounted() {
    let query = JSON.parse(window.localStorage.getItem("cityInfo"));
    this.city = query || this.$route.query;
    let history = JSON.parse(window.localStorage.getItem("history"))
    this.historyList = history || []
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    // 搜索
    async search() {
      let { data } = await this.$axios.get("http://elm.cangdu.org/v1/pois", {
        params: {
          type: "search",
          city_id: this.city.id,
          keyword: this.keyword
        }
      });
      this.flag = true
      this.keyword = ''
        this.searchList = data
    },
    // 点击列表加入历史记录，并跳转
    addHistory(city){
      this.flag = false
      this.historyList.push(city)
      if(this.historyList.length >4){
        this.historyList.splice(0,1)
      }
        window.localStorage.setItem("history",JSON.stringify(this.historyList))
        window.localStorage.setItem("City",JSON.stringify(city))

        this.$router.push({
          path:"/elm/index",
          query:city
        })
    },
    // 清空历史记录
    clearHistory(){
      window.localStorage.removeItem("history")
      this.historyList = []
    }
  },
  filters: {},
  computed: {},
  watch: {}
};
</script>

<style lang='scss' scoped>
.wsy_container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  > .wsy_top {
    position: fixed;
    top: 0;
    padding: 0 0.2rem;
    box-sizing: border-box;
    width: 100%;
    height: 46px;
    line-height: 46px;
    font-size: 18px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #3190e8;
    color: #fff;
    z-index: 100;
    > p {
      margin: 0;
    }
    > :nth-child(3) {
      font-size: 14px;
    }
  }
  > .wsy_cont {
    // flex: 1;
    margin-top: 46px;
    position: relative;
    > .van-button {
      margin-top: 10px;
    }
    >.van-icon{
      position: absolute;
      right: 20px;
      top: 15px;
    }
  }
}
.wsy_searchList {
  width: 100%;
  box-sizing: border-box;
  padding: 0 0.2rem;
  margin-top: 10px;
  >ul{
    width: 100%;
    >li{
      width: 100%;
      border-top: 1px solid #eee;
      border-bottom: 1px solid #eee;
      >:nth-child(1){
        font-size: 16px;
      }
      >:nth-child(2){
        font-size: 14px;
      }
    }
  }
}
.historyList{
  width: 100%;
  box-sizing: border-box;
  padding: 0 .2rem;
  >ul{
    width: 100%;
    >li{
      width: 100%;
      margin-top: 5px;
      border-bottom: 1px solid #eee;
    }
  }
}
</style>
