<template>
  <div class="content">
      <router-view></router-view>
      <!-- 底部tabbar区域 -->
    <van-tabbar v-model="active" active-color="#25ABFF" inactive-color="#6C6C6C">
      <van-tabbar-item icon="home-o" to='/elm/Home'>首页</van-tabbar-item>
      <van-tabbar-item icon="search" to='/elm/search'>搜索</van-tabbar-item>
      <van-tabbar-item icon="friends-o" to='/elm/indent'>订单</van-tabbar-item>
      <van-tabbar-item icon="setting-o" to='/elm/my'>我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  name:"",
  components: {

},
  props: {},
  data() {
    return {
        active:0
    };
  },
  created() {},
  mounted() {
      let active = JSON.parse(window.localStorage.getItem("Active"))
      this.active = active || 0
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to,from,next) {
      if(from.path == "/elm/Home"){
          this.active = 1
      }
      
next()
},
  methods: {

  },
  filters: {},
  computed: {
      
  },
  watch: {
      active(){
          window.localStorage.setItem("Active",JSON.stringify(this.active))
      }
  },
};
</script>

<style  scoped>
.content{
    width: 100%;
    height: 100%;
    display: flex;
}

</style>
