<template>
  <div class="wsy_container">
    <div class="wsy_header">
      <div class="wsy_top">
        <span>饿了么</span>
        <span @click="toLogin()" v-show="!isLogin">登陆/注册</span>
        <van-icon name="user-o" v-show="isLogin" @click='toMy()' />
      </div>
    </div>
    <div class="wsy_list_parent" ref="scrollBox">
      <div class="wsy_list">
        <div class="wsy_present">
          <div class="wsy_city_tip">
            <span>当前定位城市:</span>
            <span>定位不准确时，请在城市列表中选择</span>
          </div>
          <div class="wsy_city">
            <span  @click='toCityInfo(guessCity)'>{{ guessCity.name }}</span>
            <span>
              <van-icon name="arrow" />
            </span>
          </div>
        </div>
        <!-- 热门城市列表 -->
        <div class="wsy_hot_city_container">
          <p>热门城市</p>
          <ul class="hot_city_ul">
            <li v-for="(item,index) in hotCity" :key="index" @click='toCityInfo(item)'>{{ item.name }}</li>
          </ul>
        </div>
        <!-- 列表 -->
        <div class="group_item" v-for="(item,key,index) in groupList" :key="index">
          <p class="group_title">{{ key }}</p>
          <ul>
            <li v-for="(it,i) in item" :key="i"  @click='toCityInfo(it)'>{{ it.name }}</li>
          </ul>
        </div>
      </div>
    </div>
    <!-- 右侧索引栏 -->
    <div class="right_index">
      <p @click="scrollTo(index)" v-for="(item,key,index) in groupList" :key="index">{{ key }}</p>
    </div>
    <!-- 回到顶部栏 -->
    <div class="back_top" @click="backTop" v-show="flag">点击回到顶部</div>
  </div>
</template>

<script>
import BetterScroll from "better-scroll";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      guessCity: [],
      hotCity: [],
      bs: null,
      groupList: {},
      flag: false,
      right_flag: false,
      isLogin: null
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        this.bs = new BetterScroll(this.$refs.scrollBox, {
          probeType: 3,
          click:true
        });
        this.bs.on("scroll", pos => {
          if (pos.y < -200) {
            this.flag = true;
          } else {
            this.flag = false;
          }
          // if(pos.y < -340){
          //     this.right_flag = true
          // }else{
          //     this.right_flag = false
          // }
        });
      }, 800);
    });
    this.getGuess();
    this.getHotCity();
    this.getgroup();
    this.getLoginstate();
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    next();
  },
  methods: {
    //   获取当前城市信息
    getGuess() {
      this.$axios
        .get("http://elm.cangdu.org/v1/cities?type=guess")
        .then(({ data }) => {
          this.guessCity = data;
        });
    },
    //   获取热门城市数据
    getHotCity() {
      this.$axios
        .get("http://elm.cangdu.org/v1/cities?type=hot")
        .then(({ data }) => {
          // console.log(data);
          this.hotCity = data;
        });
    },
    // 获取城市列表
    getgroup() {
      this.$axios
        .get("http://elm.cangdu.org/v1/cities?type=group")
        .then(({ data }) => {
          //   console.log(data);
          //   Object.getOwnPropertyNames(data).forEach(function(key) {
          //     console.log(key + "---" + data[key]);
          //   });
          Object.keys(data)
            .sort()
            .map(key => {
              this.groupList[key] = data[key];
              // return
            });
          //   this.groupList = data;
        });
    },
    // 点击索引栏滚动到指定内容区域
    scrollTo(index) {
      console.log(index);
      let arr = document.querySelectorAll(".group_item");
      let newArr = [...arr].slice(0, index);
      let scrollHeight = 0;
      newArr.forEach(item => {
        scrollHeight += item.offsetHeight;
        // console.log(item.offsetHeight);
      });
      scrollHeight = scrollHeight + (270 - 30);
      this.bs.scrollTo(0, -scrollHeight, 1000);
    },
    // 点击回到顶部
    backTop() {
      this.flag = false;
      this.bs.scrollTo(0, 0, 1000);
    },
    // 点击跳转登陆页面
    toLogin() {
      this.$router.push("/elm/login");
    },
    // 获取登陆状态
    getLoginstate() {
      let user_Token = window.localStorage.getItem("user_Token");
      this.isLogin = user_Token ? true : false;
    },
    // 跳转到个人页面
    toMy(){
      this.$router.push("/elm/my");
    },
    // 点击热门城市跳转到城市搜索页
    toCityInfo(city){
      // console.log(city);
      this.$router.push({
        path:"/elm/info",
        query:city
      })
    window.localStorage.setItem("cityInfo",JSON.stringify(city))
    }
  },
  filters: {},
  computed: {},
  watch: {}
};
</script>

<style lang='scss' scoped>
.wsy_top {
  padding: 0 0.2rem;
  height: 46px;
  line-height: 46px;
  font-size: 18px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #3190e8;
  color: #fff;
}
.wsy_container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  > .wsy_list_parent {
    width: 100%;
    flex: 1;
    //   height: 100%;
    overflow: hidden;
    > .wsy_list {
      width: 100%;
      min-height: 101%;
      background-color: #f5f5f5;
    }
  }
}
.wsy_present {
  background-color: #fff;
  width: 100%;
  height: 100px;
  box-sizing: border-box;
  padding: 0 0.2rem;
  > .wsy_city_tip {
    font-size: 14px;
    width: 100%;
    height: 50%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    > :nth-child(2) {
      font-size: 12px;
      font-weight: bold;
      color: #ab9f9f;
    }
  }
  > .wsy_city {
    width: 100%;
    height: 50%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    > :nth-child(1) {
      color: #3190e8;
    }
  }
}
.wsy_hot_city_container {
  margin-top: 20px;
  background-color: #fff;
  width: 100%;
  height: 150px;
  display: flex;
  flex-direction: column;
  > :nth-child(1) {
    margin: 0;
    font-size: 16px;
    float: left;
    text-indent: 0.2rem;
  }
  > .hot_city_ul {
    flex: 1;
    margin-top: 0.2rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    color: #3190e8;
    > li {
      width: 25%;
      height: 20px;
      text-align: center;
    }
  }
}
.group_item {
  // padding: 0 .2rem;
  //   margin-top: 20px;
  background-color: #fff;
  font-size: 16px;
  > ul {
    box-sizing: border-box;
    padding: 0 0.4rem;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    // justify-content: space-between;
    align-items: center;
    > li {
      width: 25%;
      height: 50px;
      line-height: 50px;
      text-align: center;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
.group_title {
  margin: 0;
  text-indent: 0.2rem;
  border-bottom: 1px solid #000;
  background: #f5f5f5;
}
.right_index {
  position: fixed;
  right: 0;
  top: 150px;
  width: 20px;
  > p {
    margin: 0;
  }
}
.back_top {
  width: 100%;
  height: 30px;
  border-top: 1px solid #fff;
  text-align: center;
  position: fixed;
  top: 46px;
  background-color: #3190e8;
  color: #fff;
}
</style>
