<template>
  <div>
      <div class="parent" :class="{active:flag}" @click='toggle()'>
          <div class="child" >
              <!-- 1 -->
          </div>
      </div>
  </div>
</template>

<script>
export default {
  name:"",
  components: {

},
  props: {},
  data() {
    return {
        flag:false
    };
  },
  created() {},
  mounted() {},
  activated() {},
  update() {},
  beforeRouteUpdate(to,from,next) {

next()
},
  methods: {
      toggle(){
          this.flag = !this.flag
          this.$emit("toggle",this.flag)
      }
  },
  filters: {},
  computed: {},
  watch: {},
};
</script>

<style lang="scss"  scoped>
.parent{
    // float: right;
    position: absolute;
    right: 10px;
    top:10px;
    width: 60px;
    height: 20px;
    border-radius: 20px;
    background-color:#f5f5f5;
    .child{
        position: absolute;
        left: 0;
        width: 20px;
        height: 20px;
        border-radius:50%;
        background-color: #000;
    }
}
.active{
    background-color: orange;
    transition: all 0.2s;
    >.child{
        left: 40px;
        background-color:green ;
    }
}
</style>
